<template>
  <div class="app-container" id="alarmEvidence">
    <div class="topBox filter-container">

      <el-select v-model="listQuery.type1"  @change='changeSelect' multiple collapse-tags class="filter-item">
        <el-checkbox class="selectAll" v-model="checked" @change='selectAll'></el-checkbox>
        <el-option v-for="item in adasDmsaAlarmTypeOptions" :selected="item.value==400" :key="item.value" :label="item.key" :value="item.value"></el-option>
      </el-select>
      <el-date-picker class="filter-item" v-model="seachTime" value-format="yyyy-MM-dd HH:mm:ss" placeholder="" type="datetimerange" :picker-options="pickerOptions" range-separator="" start-placeholder="" end-placeholder=""></el-date-picker>
      <el-cascader @change="removeValid()" v-model="listQuery.imeiCar" placeholder="" :props="propsCar" :options="dataCar" :show-all-levels="false" clearable filterable collapse-tags>

      </el-cascader>

      <el-button type="primary" @click="getList" class="filter-item" icon="el-icon-search"></el-button>
      <el-button type="primary" @click="goBack" class="filter-item"></el-button>

    </div>

    <div class="tableBox"    v-loading="listLoading">
      <div class="empty-text" v-if="!list.length"></div>
      <div v-for="item in list" class="evidenceImgBox">
        <div @click="showEvidence(item)">
          <img :src="dealUrl(item.fileUrl)"></img>
          <div class="hoverLine">
            <div>{{item.vehiIdno}}</div>
            <div>{{alarmObj[item.alarmType.toString()]}}</div>
            <div>{{formatDate1(item.fileTime)}}</div>
          </div>
        </div>
      </div>
    </div>
    <!-- <div id="alarmMap">

        </div> -->
    <pagination v-show="total>0" :total="total" :page-sizes="[15,20,25,30]" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="getList" layout="prev, pager, next, sizes, total, jumper" />
    <el-dialog title="" top="20vh" :close-on-click-modal="false" :visible.sync="dialogVisible">
      <div class="detail">
        <div class="detailBox">
          <div>{{alarmDetail.vehiIdno}}</div>
          <div>{{alarmDetail.devIdno}}</div>
          <div>{{alarmObj[alarmDetail.alarmType]}}</div>
          <div>{{formatDate1( alarmDetail.fileTime)}}</div>
          <div>{{alarmDetail.jingDu?(alarmDetail.jingDu/1000000+','+alarmDetail.weiDu/1000000):''}}</div>
          <div>{{alarmDetail.position}}</div>
          <div><img :src="dealUrl(alarmDetail.fileUrl)" alt=""></div>
          <!-- <el-button type="primary" size="mini"> <a :href="alarmDetail.downloadUrl+'.zip'" :download="alarmDetail.devIdno+'-'+alarmObj[alarmDetail.alarmType]+'-'+formatDate1( alarmDetail.fileTime)+'.zip'"></a></el-button> -->

        </div>
        <div id="alarmMap">
        </div>
      </div>

    </el-dialog>
  </div>

</template>
<script>

