        { key: "", value: 1 },
        { key: "", value: 2 },
        { key: "", value: 3 },
        { key: "", value: 4 },
        { key: "", value: 5 },
        { key: "2", value: 6 },
        { key: "", value: 7 },
        // { key: "", value: 8 },
        { key: "", value: 9 },
        { key: "", value: 10 },
        { key: "", value: 11 },
        { key: "", value: 12 },
        { key: "", value: 13 },
        { key: "", value: 14 },
        { key: "", value: 15 },
        { key: "", value: 16 },
        { key: "ADASDSM", value: 17 },
      ],

      adasDmsaAlarmTypeOptions: [
        { key: "1", value: 624 },
        { key: "2", value: 625 },
        { key: "", value: 400 },
        { key: "", value: 401 },
        { key: "", value: 402 },
        { key: "", value: 403 },
        { key: "", value: 404 },
        { key: "1", value: 618 },
        { key: "2", value: 619 },

        { key: "1", value: 620 },
        { key: "2", value: 621 },
        { key: "1", value: 622 },
        { key: "2", value: 623 },

        { key: "1", value: 626 },
        { key: "2", value: 627 },
        { key: "1", value: 630 },
        { key: "2", value: 631 },
      ],
