      if (status == 0) {
        return "";
      } else if (status == 1) {
        return "";
      } else if (status == 2) {
        return "";
      } else if (status == 3) {
        return "";
      }
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    },
    // 
    getNetworkByRoute() {
      return new Promise((resolve, reject) => {
        destinationByRoad({ routeId: this.roadId })
          .then(res => {

            resolve(res.data)
          }).catch(error => {

          })
          .finally(() => {

          });
      });

