              startTime: s,
              endTime: e
            })
              .then(res => {
                // console.log(res)
                if (res.success) {
                  if (
                    res.data == null ||
                    res.data.length == 0 ||
                    res.data.filter(
                      x =>
                        x.lat != null &&
                        x.lng != null &&
                        x.lat * 1 > 1 &&
                        x.lng * 1 > 1
                    ).length == 0
                  ) {
                    _t.$message.warning("");
                    _t.showDelete = false; // 
                  } else {
                    // pointArr
                    // mlng  mlat 
                    _t.showDelete = true; // 
                    let datas = [];
                    datas = res.data
                      .filter(
                        x =>
                          x.lat != null &&
                          x.lng != null &&
                          x.lat * 1 > 1 &&
                          x.lng * 1 > 1
                      )
                      .map(x => {
                        let point = Gps2Bd(new BMap.Point(x.lng, x.lat)); // 
                        return `${point.lng} ${point.lat}`;
                      });
                    // console.log('');
                    // console.log(datas);
                    // 
                    let myData = { wktGps: datas };
                    simplify(myData).then(results => {
                      if (results.success) {
                        // 
                        // console.log(results);
                        let geom = results.data.slice(11, -1).split(",");
                        // console.log('');
                        // console.log(geom);
                        geom.forEach(x => {
                          let newPointArr = x.split(" ");
                          _t.pointArr.push(
                            new BMap.Point(x.split(" ")[0], x.split(" ")[1])
                          );
                        });
                        // console.log( _t.pointArr);
                        _t.$refs.temp.validateField("roadName"); // 
                        _t.map.setViewport(_t.pointArr, {
                          margins: [100, 100, 100, 100]
                        });
                        _t.getNewPoint();
                      } else {
                        _t.$message.error(results.msg);
                      }
                    });
                  }
                } else {
                  _t.$message.error(res.msg);
                }
              })
              .finally(() => {
                _t.lineLoading = false;
              });
          }
        });
      });
    },
    // 
    addRoute(formName) {
      let _t = this;
      _t.$nextTick(() => {
        _t.$refs[formName].validate(valid => {
          if (valid) {
            let myData = {
              name: _t.temp.roadName,
              crosslobby: JSON.stringify(_t.temp.network),
              wktGps: _t.pointStrArr
            };
            create(myData)
              .then(res => {
                if (res.status == 0) {
                  _t.$message.success("");
                  _t.isRefresh = true;
                  _t.initMap();
                } else {
                  _t.$message.error(res.msg);
                }
              })
              .finally(() => {
              });
          }
        });
      });
    },
    changeLoadType(type) {
      // 
      if (type == 0) {
        this.loadType = "";
        this.handOrGet = false;
      } else if (type == 1) {
        this.loadType = "";
        this.handOrGet = true;
      }
      // this.handOrGet = !this.handOrGet;
      this.initMap();
      if (type == 0) {
        this.handDraw();
      }
    },
    handDraw() {
      // 
      let _t = this,
        drawingManager = new BMapLib.DrawingManager(_t.map, {
          //
          enableDrawingTool: true,
          polylineOptions: _t.lineOptions,
          drawingToolOptions: {
            drawingModes: [BMAP_DRAWING_POLYLINE]
          }
        });
      drawingManager.addEventListener("polylinecomplete", function (overlay) {
        // 
        _t.showDelete = true; // 
        // _t.pointArr = overlay.getPath();// 
        if (_t.drawStart == 2) {
          _t.pointArr = _t.pointArr.concat(overlay.getPath());
        } else if (_t.drawStart == 1 && _t.pointArr.length) {
          let reverseArr = [...overlay.getPath()].reverse();
          _t.pointArr = reverseArr.concat(_t.pointArr);
        } else {
          _t.pointArr = overlay.getPath();
        }
        // _t.map.removeOverlay(overlay);// 
        _t.map.clearOverlays();
        _t.$refs.temp.validateField("roadName"); // 
        _t.getNewPoint();
        // document.getElementsByClassName('BMapLib_Drawing_panel')[0].style.display = 'none';// 
      });
    },
    getNewPoint() {
      // 
      let _t = this,
        map = _t.map,
        count = 0, // --
        marks = [], //  [H{lng:123,lat:456}]--
        clickIndexArr = [], //  [1,6]--
        polyline = undefined, // 
        pointCollection = undefined, // 
        pointCArr = [..._t.pointArr], // copy [H{lng:123,lat:456}]--
        editLine = undefined, // 
        editLineArr = []; //  [H{lng:123,lat:456}]--

      // 
      polyline = new BMap.Polyline(_t.pointArr, _t.lineOptions);
      map.addOverlay(polyline);
      // 
      var marker1 = new BMap.Marker(_t.pointArr[0], { icon: _t.markerStart }); // 
      map.addOverlay(marker1);
      var marker2 = new BMap.Marker(_t.pointArr[_t.pointArr.length - 1], {
        icon: _t.markerEnd
      }); // 
      map.addOverlay(marker2);
      pointCollection = new BMap.PointCollection(_t.pointArr, {
        color: "#d340c3",
        size: BMAP_POINT_SIZE_SMALL
      });
      map.addOverlay(pointCollection);
      // 
      pointCollection.addEventListener("click", function (e) {
        let clickP = e.point, // 
          clickPoint = `${clickP.lng} ${clickP.lat}`, // 
          clickIndex = _t.pointStrArr.indexOf(clickPoint), // 
          markoption = {
            icon: new BMap.Symbol(BMap_Symbol_SHAPE_CIRCLE, {
              scale: 7,
              fillColor: "red",
              fillOpacity: 1,
              strokeWeight: 1
            })
          }; // 
        ///////////////////
        if (marks.length < 2) {
          clickIndexArr.push(clickIndex); // 
          editLineArr.push(clickP); // 
          marks[count] = new BMap.Marker(clickP, markoption); // 
          map.addOverlay(marks[count++]);
        }
        if (count == 2) {
          // ,
          count = 3; // 
          if (clickIndexArr[0] > clickIndexArr[1]) {
            // 
            clickIndexArr.reverse();
            marks.reverse();
            editLineArr.reverse();
          }
          marks[0].setIcon(new BMap.Icon(rightpng, new BMap.Size(16, 16))); // 
          marks[1].setIcon(new BMap.Icon(errorpng, new BMap.Size(16, 16))); // 
          // 
          if (clickIndexArr[0] == 0) {
            marks[0].setOffset(new BMap.Size(12, 12));
          }
          if (clickIndexArr[1] == _t.pointArr.length - 1) {
            marks[1].setOffset(new BMap.Size(12, 12));
          }
          // editLineArr
          editLine = new BMap.Polyline(editLineArr, {
            strokeColor: "red",
            enableEditing: true
          });
          map.addOverlay(editLine); // 
          pointCArr.splice(
            clickIndexArr[0],
            clickIndexArr[1] - clickIndexArr[0] + 1
          ); // 
          pointCollection.setPoints(pointCArr); // 
          // 
          editLine.addEventListener("lineupdate", function (e) {
            editLineArr = e.target.getPath(); // editLineArr
          });

          marks.forEach((x, i) => {
            // 
            x.addEventListener("click", function () {
              if (i == 0) {
                // 
                _t.pointArr.splice(
                  clickIndexArr[0],
                  clickIndexArr[1] - clickIndexArr[0] + 1
                ); // 
                editLineArr.forEach((y, j) => {
                  _t.pointArr.splice(clickIndexArr[0] + j, 0, y); // editLineArr
                });
              }
              map.removeOverlay(marks[0]); // 
              map.removeOverlay(marks[1]);
              map.removeOverlay(editLine); // 
              map.removeOverlay(polyline); // 
              pointCollection.clear(); // 
              count = 0; // 
              marks = [];
              clickIndexArr = [];
              editLineArr = [];
              pointCArr = [..._t.pointArr];
              polyline = new BMap.Polyline(_t.pointArr, _t.lineOptions); // 
              map.addOverlay(polyline);
              pointCollection.setPoints(_t.pointArr); // 
              // console.log('');
              // console.log(_t.pointStrArr);
              // 
              marker1.setPosition(_t.pointArr[0]);
              marker2.setPosition(_t.pointArr[_t.pointArr.length - 1]);
            });
          });
        }
      });
    },
    deleteRoute() {
      // 
      this.map.clearOverlays();
      this.pointArr = [];
      this.showDelete = false;
      let BMapLib_Drawing_panel = document.getElementsByClassName(
        "BMapLib_Drawing_panel"
      )[0];
      if (BMapLib_Drawing_panel) {
        BMapLib_Drawing_panel.style.display = "block"; // 
      }
    },

    getDevlist() {
      // 
      var that = this;
      teamAndCarAllBindList({}).then(res => {
        if (res.status == 0) {
          that.setCarLineOptions(res); // 
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    setCarLineOptions(res) {
      // 
      let t = this,
        result = [],
        resObj = res.data;
      for (let i in resObj) {
        if (resObj[i].length > 0) {
          // 
          // resObj[i].map(x=>x.showName = `${x.licenseNo}(${x.serialNo})`);
          resObj[i].forEach(x => {
            // t.getCarImei[x.licenseNo] = x.serialNo; // 
            t.getCarImei[x.licenseNo] = x.licenseNo; // 
            x.title = x.serialNo;
          });
          result.push({
            // serialNo: i == "" ? "" : i,// showName
            licenseNo: i == "" ? "" : i,
            children: resObj[i]
          });
        }
      }
      this.carList = result;
    },
    // goBack() {
    //   // 
    //   this.$emit("closePDial", this.isRefresh);
    // },
    //
    getSelect() {
      let that = this;

      allDestination({ type: -1 }).then(res => {
        if (res.status == 0) {
          if (res.data.records === null) {
            // that.networkOptions = [{ id: -1, name: "" }];
            return;
          }
          that.networkOptions = res.data;
          // that.networkOptions.unshift({ id: -1, name: "" });
        } else {
          if (res.data == null) {
            this.networkOptions = [];
          }
          this.$message.error(res.msg);
        }
      });



    },
  }
};
</script>
