            <span>{{ formatTime(row.createTime)}}</span>
          </template>
        </el-table-column>

        <el-table-column label="" width="230px" align="center">
          <template slot-scope="{row}">
            <span>{{ formatTime(row.delayTime)}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="150px" align="center">
          <template slot-scope="{row}">
            <span>{{ row. handlerName}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="230px" align="center">
          <template slot-scope="{row}">
            <span>{{ formatTime(row.handlerTime)}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="200px" align="center">
          <template slot-scope="{row}">
            <span>{{ row. handlerInfo}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="110px" align="center">
          <template slot-scope="{row}">
            <span :class="{'red':row.handler==0}">{{ alarmStatus(row.handler)}}</span>
          </template>v-show=
        </el-table-column> -->
        <el-table-column label="" fixed="right" align="center" min-width="250" class-name="small-padding fixed-width">
          <template slot-scope="{row}">

            <!-- <el-button v-if="(row.handler===0||row.handler===2)&&row.uuid" type="primary" size="mini" @click="handleUpdate(row,'deal')" icon="iconfont iconchuli"></el-button> -->
            <el-button type="primary" @click="checkEvidence(row)" class="checkbtn" size="mini" icon="iconfont iconchakan"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div v-if="!mediaNone" class="onlineTime">
      <span>:</span>
      {{imei1}}
    </div>
    <div v-if="!mediaNone" class="onlineTime">
      <span v-if="onlineTime">:</span>
      {{onlineTime?formatTime1(onlineTime):''}}
    </div>
    <el-table :key="tableKey" v-loading="listLoading" :data="list1" max-height="800" fit highlight-current-row style="width: 100%;" v-if="!mediaNone" :row-class-name="tableRowClassName">
      <el-table-column label="" prop="id" sortable="custom" align="center" width="80" type="index" :index="indexMethod"></el-table-column>

      <el-table-column label="" min-width="230px" align="center">
