    <pagination :page-sizes="[10,10,30]" v-if="mediaNone" v-show="total>0" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="getList" layout="prev, pager, next, sizes, total, jumper" />

    <el-dialog v-if="mediaNone" :class="{'delayStyle':(dialogStatus!=='deal')}" :title="textMap[dialogStatus]" :visible.sync="dialogFormVisible" center id="myDialog">
      <el-form ref="dataForm" :rules="rules" :model="temp" label-position="left">
        <el-form-item label="()" prop="time" v-if="dialogStatus!=='deal'">
