        </el-form-item>
        <el-form-item label="" prop="info" class="info" v-if="dialogStatus==='deal'">
          <el-input type="textarea" v-model="temp.info" maxlength="300" />
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogStatus==='deal'?dealData():delayData()"></el-button>
