<script>
function debounce(func, wait = 1000) {
  let timeout;
  return function (event) {
    clearTimeout(timeout)
    timeout = setTimeout(() => {
      func.call(this, event)
    }, wait)
  }
}
import Pagination from "@/components/Pagination"; // secondary package based on el-pagination
//    
import {
  alarmListDay,
  alarmDelay,
  alarmHandler,
  checkDevice,
  exportBigExcel,
} from "../../../api/alarmList";
import {
  teamAndCarAllBindList,
  teamAndGunList
} from "../../../api/realTimeMonitoring";
import { formatDate, renderTime } from "../../../../src/common"; //
import { parse } from 'protobufjs';

export default {
  name: "alarmRcord",
  components: { Pagination },
  data() {
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 
      imei: "8669560400", //
      imei1: "", //
      tableKey: 0,
      list: [], //
      list1: [], //
      total: 0, //
      listLoading: false,
      listQuery: {
        page: 1,
        limit: 10,
        type: [],
        status: -1,
        type1: [400, 401, 402, 403, 404, 618, 619, 620, 621, 622, 623, 624, 625, 626, 627, 630, 631],
        keyword: "", //
        imeiCar: [],
        imeiGun: []
      },
      exportData: {
        // dates: null, //null
        // excelType: 18, //18
        // licenseNo: null, //null
        // title: "", //
        // expType: null, //
        expStatus: null, //
        tableName: "", //yyyy_MM
        days: "", //
        // keyword: null //pagekeyword
      }, //
      indexMethod: 1, //
      temp: {}, //
      dialogFormVisible: false, //
      dialogStatus: "", //
      textMap: {
        deal: "",
        check: ""
      },
      rules: {
        time: [{ required: true, message: "", trigger: "blur" }],
        info: [{ required: true, message: "", trigger: "blur" }]
      }, //
      //
      alarmTypeOptions: [
        // { key: "", value: 1 },
        // { key: "", value: 2 },
        { key: "", value: 3 },
        { key: "", value: 4 },
        { key: "", value: 5 },
        // { key: "2", value: 6 },
        // { key: "", value: 7 },
        // { key: "", value: 9 },
        // { key: "", value: 10 },
        { key: "", value: 11 },
        { key: "", value: 12 },
        { key: "", value: 13 },
        // { key: "", value: 14 },
        { key: "", value: 15 },
        { key: "", value: 16 },
        { key: "ADASDSM", value: 17 },
        { key: "", value: 701 },
        // { key: "", value: 711 },
      ],

      adasDmsaAlarmTypeOptions: [
        { key: "", value: 400 },
        { key: "", value: 401 },
        { key: "", value: 402 },
        { key: "", value: 403 },
        // { key: "", value: 404 },
        { key: "1", value: 618 },
        { key: "2", value: 619 },
        { key: "1", value: 620 },
        { key: "2", value: 621 },
        { key: "1", value: 622 },
        { key: "2", value: 623 },
        { key: "1", value: 624 },
        { key: "2", value: 625 },
        { key: "1", value: 626 },
        { key: "2", value: 627 },
        // { key: "1", value: 630 },
        // { key: "2", value: 631 },
      ],
      alarmObj: {
        1: '',
        2: '',
        3: '',
        4: '',
        5: '',
        6: '2',
        7: '',
        9: '',
        10: '',
        11: '',
        12: '',
        13: '',
        14: '',
        15: '',
        16: '',
        //
        400: '',
        401: '',
        402: '',
        403: '',
        404: '',
        618: '1',
        619: '2',
        620: '1',
        621: '2',
        622: '1',
        623: '2',
        624: '1',
        625: '2',
        626: '1',
        627: '2',
        630: '1',
        631: '2',
        //
        701: '',
        711: ''
      },
      //
      alarmStatusOptions: [
        { key: "", value: -1 },
        { key: "", value: 0 },
        { key: "", value: 1 },
        { key: "", value: 2 },
        { key: "", value: 3 }
      ],
      seachTime: this.formatDateTime(new Date()), //
      seachTime1: this.formatDateTime(new Date()), //
      carOrGun: null, //
      onlineTime: null, //
      AdasAndDsmType: 17,//ADASDMS

      propsCar: {
        expandTrigger: "hover",
        multiple: true
      },
      propsGun: {
        expandTrigger: "hover",
        multiple: true
      },
      dataCar: [], // 
      dataGun: [], // ,
      imei2name: {}, // imei
      gunAlarmType: [1, 3, 4, 10, 12],
      carAlarmType: [2, 5, 6, 7, 9, 11, 13, 14, 15, 16, 17],
      carAlarmTypeVal: 'car',
      tableBoxType: 'NOADAS',

      checked: true,
      selectedArray: [],
      exportLoading: false

    };
  },
  props: ["mediaNone"], // 

  created() {

    if (this.mediaNone) {
      // this.getList();
      this.selectAll();
      let p1 = this.getTeamAndCarList();
      let p2 = this.getTeamAndGunList();
      Promise.all([p1, p2]).then((values) => {

        this.getList();
      })

    } else {
    }
  },
  watch: {
    //
    "$store.state.realTimeMonitor.newSocketData": {
      handler: function (newer, older) {
        let that = this,
          newSocketData = that.$store.state.realTimeMonitor.newSocketData; //
        if (JSON.stringify(newSocketData) == "{}") {
          return;
        }
        let storeData = {};
        let brokenData = {};
        if (newSocketData.status == 10000) {
          storeData = { ...newSocketData };
          // console.warn('10000')
          if (newSocketData.message.imei == that.imei1) {
            // console.warn('')

            that.onlineTime = Date.now();
          }
        }
        // //
        // if (newSocketData.status == 10008) {
        //   console.warn('')
        //   brokenData = { ...newSocketData };
        //   if (newSocketData.message.imei == that.imei1) {
        //     // console.warn('')

        //     that.brokenTime = newSocketData.message.imei;
        //   }
        // }
      },
      deep: true // 
    }
  },
  methods: {
    selectAll() {
      this.listQuery.type = []
      if (this.checked) {
        this.alarmTypeOptions.map((item) => {
          this.listQuery.type.push(item.value)
        })
      } else {
        this.listQuery.type = []
      }
    },
    changeSelect(val) {
      if (val.length === this.adasDmsaAlarmTypeOptions.length) {
        this.checked = true
      } else {
        this.checked = false
      }
    },
    checkEvidence(row) {
      let startTime = this.formatTime(row.createTime)
      let endTime = this.formatTime(row.createTime)//3s
      // console.warn(this.formatTime(new Date(endTime).getTime()+5000));
      // let carNoObj=this.$store.state.realTimeMonitor.carAndGunObj[row.devId];
      this.$router.push({ path: '/alarmEvidence', query: { devId: row.objectId, alarmType: row.expType, alarmTime: this.formatTime(new Date(startTime).getTime() - 30000), endTime: this.formatTime(new Date(endTime).getTime() + 30000) } })
    },
    //
    getGunOrCarType(val) {
      if (this.gunAlarmType.indexOf(val) !== -1) {
        this.carAlarmTypeVal = 'gun'

      } else if (this.carAlarmType.indexOf(val) !== -1) {
        this.carAlarmTypeVal = 'car'

      }

      if (val.length === this.alarmTypeOptions.length) {
        this.checked = true
      } else {
        this.checked = false
      }

    },
    //
    removeValid() {
      // 
      // this.$refs.mdForm.clearValidate(["imeiCar", "imeiGun"]);
    },
    //tree
    getTeamAndCarList() {
      let that = this;
      return new Promise((resolve, reject) => {
        teamAndCarAllBindList({}).then(res => {
          if (res.status == 0) {
            that.dataCar = this.formatCas(res.data, "serialNo", "licenseNo"); // 
            //
            if (this.$route.params && this.$route.params.imeiCar) {
              that.listQuery.imeiCar = this.$route.params.imeiCar;
              that.listQuery.type = [17]
            } else {
              for (let i = 0; i < this.dataCar.length; i++) {
                for (let j = 0; j < this.dataCar[i].children.length; j++) {
                  that.listQuery.imeiCar.push([that.dataCar[i].value, this.dataCar[i].children[j].value]);
                }
              }
            }
            resolve();
          } else {
            this.$message.error(res.msg);
          }
        });
      })

    },
    // 
    getTeamAndGunList() {
      let that = this;
      return new Promise((resolve, reject) => {
        teamAndGunList({}).then(res => {
          if (res.status == 0) {
            that.dataGun = this.formatCas(res.data, "iMei", "code"); // 
            if (this.$route.params && this.$route.params.imeiCar) {
              that.listQuery.imeiGun = []
              that.listQuery.type = [17]
            } else {
              for (let i = 0; i < this.dataGun.length; i++) {
                for (let j = 0; j < this.dataGun[i].children.length; j++) {
                  that.listQuery.imeiGun.push([that.dataGun[i].value, this.dataGun[i].children[j].value]);
                }
              }
            }

            resolve();
          } else {
            this.$message.error(res.msg);
          }
        });
      })

    },
    formatCas(data, imei, name) {

      //  data imeivaue namelabel
      let result = [],
        id = 0,
        _t = this;
      for (let i in data) {
        let childs = []; // 
        if (i == "") { continue }
        if (i == "") { continue }

        data[i].forEach(x => {
          //
          if (x[imei] !== "") {

            childs.push({
              value: x[imei],
              label: x[name],
            });
            _t.imei2name[x[imei]] = x[name]; // 
          }
        });
        result.push({
          value: `i${id++}`,
          label: i == "" ? "" : i,
          children: childs
        });
      }
      return result;

    },
    getDevice() {
      this.listLoading = true;
      this.imei1 = this.imei;
      this.onlineTime = null;
      // gunOnline({ imei: this.imei }).then(res => {
      //   if (res && res.status == 0) {
      //     this.onlineTime = res.data;
      //   } else {
      //     this.$message.error(res.msg);
      //   }
      // });
      //
      checkDevice({ imei: this.imei1 }).then(response => {
        if (response.data == null) {
          this.list1 = [];
        } else {
          this.list1 = response.data;
        }
        this.listLoading = false;
      });
    },
    exportDo: debounce(function (e) {
      this.exportExcel();
    }),
    //
    exportExcel() {
      this.exportLoading = true;
      this.$message.warning(",,");
      this.exportData.tableName = this.seachTime.split("_")[0] + "_" + this.seachTime.split("_")[1];
      let date = this.seachTime, arr = [], obj = {};
      this.exportData.expKeywords = []

      this.exportData.days = this.seachTime.split("_")[2];
      this.exportData.expTypes = [...this.listQuery.type];

      let index = this.exportData.expTypes.indexOf(17);
      if (index !== -1) {
        this.exportData.expTypes.splice(index, 1);
        this.exportData.expTypes = this.exportData.expTypes.concat(this.listQuery.type1)
      }
      // if (this.listQuery.type !== this.AdasAndDsmType) {
      //   this.exportData.expType = this.listQuery.type;

      // } else {
      //   this.exportData.expType = this.listQuery.type1;

      // }
      // if (this.carAlarmTypeVal == 'gun') {
      //   arr = this.listQuery.imeiGun
      // } else {
      //   arr = this.listQuery.imeiCar


      // }
      arr = this.listQuery.imeiGun.concat(this.listQuery.imeiCar)
      for (let i = 0; i < arr.length; i++) {
        this.exportData.expKeywords.push(arr[i][1])
      }
      this.exportData.expStatus = this.listQuery.status;
      // this.exportData.expKeywords = this.listQuery.keyword;
      let type = this.exportData.expType;
      let status = this.exportData.expStatus;
      // let keyword = this.exportData.keyword
      // console.warn(this.exportData)

      // return;
      obj = { ...this.exportData }
      obj.excelType = '1001'
      // if (this.listQuery.type !== this.AdasAndDsmType) {
      //   obj = { ...this.exportData }
      //   obj.excelType = '1001'
      // } else {
      //   let newDataStr = this.seachTime.replace(/\.|\_/g, '/')
      //   obj.timestamp = new Date(newDataStr).getTime();
      //   obj.expTypes = this.exportData.expType;
      //   obj.excelType = '1002'
      // }
      obj.positive = false;
      obj.title = '';
      obj.expKeywords = (this.exportData.expKeywords.length ? this.exportData.expKeywords : null)

      exportBigExcel(obj).then((response, a, b) => {
        try {
          let enc = new TextDecoder('utf-8')
          let data = JSON.parse(enc.decode(new Uint8Array(res)))
          this.$message({
            message: data.msg,
            type: 'warning'
          });
          // if (response.msg) {
          //   this.$message({
          //     message: '',
          //     type: 'warning'
          //   });
          // }

        } catch (error) {
          let blob = new Blob([response], { type: "application/excel" }); // pdf
          let link = document.createElement("a");
          let typeText, statusText;
          for (let i = 0; i < this.alarmTypeOptions.length; i++) {
            if (this.listQuery.type == this.alarmTypeOptions[i].value) {


              typeText = this.alarmTypeOptions[i].key

              if (this.listQuery.type == this.AdasAndDsmType) {

                for (let j = 0; j < this.adasDmsaAlarmTypeOptions.length; j++) {
                  if (this.listQuery.type1 == this.adasDmsaAlarmTypeOptions[j].value) {

                    // typeText += ('-' + this.adasDmsaAlarmTypeOptions[j].key)
                  }
                }
              }
              break;
            }
          }
          for (let j = 0; j < this.alarmStatusOptions.length; j++) {
            if (status == this.alarmStatusOptions[j].value) {
              statusText = this.alarmStatusOptions[j].key;
              break;
            }
          }
          let fileName;
          fileName = date + "----" + "----" + statusText + "----" + "" + ".xlsx";

          // if (obj.expKeywords) {
          //   fileName = obj.expKeywords.toString() + "----" + date + "----" + typeText + "----" + statusText + "----" + "" + ".xlsx";
          // } else {
          //   fileName = date + "----" + typeText + "----" + statusText + "----" + "" + ".xlsx";
          // }

          link.href = window.URL.createObjectURL(blob);
          link.download = fileName;
          link.click();
          link.remove();
        }

      }).finally(() => {
        this.exportLoading = false;
      });
    },
    //
    formatDateTime(date) {
      var y = date.getFullYear();
      var m = date.getMonth() + 1;
      m = m < 10 ? "0" + m : m;
      var d = date.getDate();
      // d = d < 10 ? "0" + d : d;
      var h = date.getHours();
      var minute = date.getMinutes();
      minute = minute < 10 ? "0" + minute : minute;
      return y + "_" + m + "_" + d;
    },
    //
    getList() {
      this.seachTime1 = this.seachTime;
      this.listLoading = true;
      this.listQuery.tableName =
        this.seachTime.split("_")[0] + "_" + this.seachTime.split("_")[1];
      this.listQuery.days = this.seachTime.split("_")[2];
      // if (this.listQuery.type !== this.AdasAndDsmType) {
      //   this.tableBoxType = 'NOADAS'
      // } else {
      //   this.tableBoxType = 'ADAS'

      // }

      let arr = [], imeis = [], obj = {}, req = null;


      // if (this.carAlarmTypeVal == 'gun') {
      //   arr = this.listQuery.imeiGun
      // } else {
      //   arr = this.listQuery.imeiCar


      // }

      arr = this.listQuery.imeiCar.concat(this.listQuery.imeiGun)
      for (let i = 0; i < arr.length; i++) {
        imeis.push(arr[i][1])
      }
      obj.page = this.listQuery.page;
      obj.limit = this.listQuery.limit;

      // if (this.listQuery.type == this.AdasAndDsmType) {
      //   let newDataStr = this.seachTime.replace(/\.|\_/g, '/')
      //   obj.timestamp = new Date(newDataStr).getTime();

      //   obj.types = this.listQuery.type1;

      //   if (arr.length) {
      //     obj.deviceIds = imeis;
      //     // obj.deviceIds = ['5010622'];

      //   }
      //   req = alarmListAdasDsm;
      // } else {

      obj.types = [...this.listQuery.type];

      let index = obj.types.indexOf(17);
      if (index !== -1) {
        obj.types.splice(index, 1);
        obj.types = obj.types.concat(this.listQuery.type1)
      }
      obj.status = this.listQuery.status;
      obj.tableName = this.listQuery.tableName;
      obj.days = this.listQuery.days;
      obj.positive = false;
      if (arr.length) {
        obj.keywords = imeis;
      }
      req = alarmListDay;
      // }

      req(obj).then(response => {
        // if (this.listQuery.type !== this.AdasAndDsmType) {
        this.list = response.data ? response.data.records : [];
        this.total = response.data.total;
        // } else {
        //   this.list = response.data;
        //   this.total = response.count;

        // }
        // this.list = this.list.sort(function (a, b) {

        //   return Number(b.alarmTime) - Number(a.alarmTime);
        // })
        this.indexMethod = (this.listQuery.page - 1) * this.listQuery.limit + 1;
        // if (
        //   this.listQuery.type == 1 ||
        //   this.listQuery.type == 3 ||
        //   this.listQuery.type == 4 ||
        //   this.listQuery.type == 8 ||
        //   this.listQuery.type == 10 ||
        //   this.listQuery.type == 12
        // ) {
        //   //
        //   this.carOrGun = "";
        // } else {
        //   //
        //   this.carOrGun = "";
        // }
        // Just to simulate the time of the request
        // setTimeout(() => {
        //   this.listLoading = false;
        // }, 1.5 * 1000);
        this.listLoading = false;
      }).finally(() => {
        this.listLoading = false;
      });
    },




    //
    resetTemp() {
      this.temp = {
        uuid: null,
        info: null
      };
    },
    //
    dealData() {
      var that = this;
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          alarmHandler({
            uuids: { [this.seachTime1]: [this.temp.uuid] },
            info: { handlerInfo: this.temp.info }
          }).then(() => {
            that.getList();
            that.dialogFormVisible = false;
            that.$notify({
              title: "Success",
              message: "",
              type: "success",
              duration: 2000
            });
          });
          this.dialogFormVisible = false;
        }
      });
    },
    //
    handleUpdate(row, type) {
      this.resetTemp();
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
      //
      this.temp.uuid = row.uuid;
      if (type == "deal") {
        this.temp.timeStamp = new Date(row.createTime).getTime();
        this.dialogStatus = "deal";
      } else {
        //
        this.temp.uuid = row.uuid;
        this.dialogStatus = "check";
      }

      this.dialogFormVisible = true;
    },
    //
    delayData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          alarmDelay({ uuids: [this.temp.uuid], time: this.temp.time }).then(
            () => {
              this.getList();
              this.dialogFormVisible = false;
              this.$notify({
                title: "Success",
                message: "Update Successfully",
                type: "success",
                duration: 2000
              });
            }
          );
          this.dialogFormVisible = false;
        }
      });
    },
    formatTime1(time) {
      return formatDate(time, 1);
    },

    formatTime(time) {
      return renderTime(time);
    },
    //
    alarmStatus(status) {
      if (status == 0) {
        return "";
      } else if (status == 1) {
        return "";
      } else if (status == 2) {
        return "";
      } else if (status == 3) {
        return "";
      }
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    }
  }
};
